import React from 'react'
import { Link } from 'react-router-dom'

const HeaderComponent = () => {
  return (
    <div>
        {/* HEADER CỦA TRANG CHỦ */}
        <header style={{ zIndex: '999' }}>
                <nav className="bg-black border-gray-200 px-4 py-2 lg:px-6 dark:bg-gray-800 h-16">
                    <div className="flex flex-wrap justify-between items-center mx-auto max-w-screen-xl">
                        {/* THe left of header */}
                        <a href="/" className="flex items-center">
                            <img src={require('../assets/images/logothuonghieu.png')}
                                className="mr-3 md:absolute top-0 left-0 w-60 h-150 z-50" alt="Flowbite Logo" />
                        </a>
                        {/* The right of header */}
                        <div className="flex items-center lg:order-2">
                            <i className='bx bx-cart-alt' style={{ color: '#fff', fontSize: '40px' }}></i>                           
                            <Link to="/dangky"
                                className="text-lg text-white hover:bg-gray-50 hover:text-black rounded-lg px-4 py-2 mr-2">
                                Đăng Ký
                            </Link>
                            <Link to="/dangnhap"
                                className="text-lg text-white hover:bg-gray-50 hover:text-black rounded-lg px-4 py-2 mr-2">
                                Đăng Nhập
                            </Link>
                            <button data-collapse-toggle="mobile-menu-2" type="button"
                                className="inline-flex items-center p-2 ml-1 text-sm text-gray-500 rounded-lg lg:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600"
                                aria-controls="mobile-menu-2" aria-expanded="false">
                                <span className="sr-only">Open main menu</span>
                                <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 20 20"
                                    xmlns="http://www.w3.org/2000/svg">
                                    <path fillRule="evenodd"
                                        d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
                                        clipRule="evenodd"></path>
                                </svg>
                                <svg className="hidden w-6 h-6" fill="currentColor" viewBox="0 0 20 20"
                                    xmlns="http://www.w3.org/2000/svg">
                                    <path fillRule="evenodd"
                                        d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                        clipRule="evenodd"></path>
                                </svg>
                            </button>
                        </div>

                        {/* Center of header */}
                        <div className="justify-between items-center w-full lg:flex lg:w-auto lg:order-1" id="mobile-menu-2">
                            <ul className="flex flex-col mt-4 font-medium lg:flex-row lg:space-x-8 lg:mt-0">
                                <li>
                                    <Link to="/"
                                        className="text-xl block py-2 pr-4 pl-3 text-white rounded bg-primary-700 lg:bg-transparent lg:text-primary-700 lg:p-0 dark:text-white">
                                        Trang Chủ
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/dmxe"
                                        className="text-xl block py-2 pr-4 pl-3 text-white rounded bg-primary-700 lg:bg-transparent lg:text-primary-700 lg:p-0 dark:text-white">
                                        Danh Mục Xe
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/lienhe"
                                        className="text-xl block py-2 pr-4 pl-3 text-white rounded bg-primary-700 lg:bg-transparent lg:text-primary-700 lg:p-0 dark:text-white">
                                        Liên Hệ
                                    </Link>
                                </li>
                                <li>
                                <Link to="/dscuahang"
                                        className="text-xl block py-2 pr-4 pl-3 text-white rounded bg-primary-700 lg:bg-transparent lg:text-primary-700 lg:p-0 dark:text-white">
                                        Cửa Hàng
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </header>
    </div>
  )
}

export default HeaderComponent